import React from 'react'
import { Typography } from 'antd'
import ModalImage from "react-modal-image";
import {Link} from 'gatsby';
import LandingLayout from '@layouts/LandingLayout'


export default () => (
  <LandingLayout activeTab = '2' lState={true}>
    <div className='flex flex-col items-center'>
      <Typography.Title className='text-primary-color'>
        SecureT - personal encryption made easy
      </Typography.Title>
      <Typography.Title level={4} className='text-gray-600'>
        SecureT allows you to generate your own encryption keys and encrypt and decrypt messages with them. The app provides assurance that your messages will remain private and that your privacy is in your own hands. Download SecureT on the Play Store or enroll in the open iOS beta now.
      </Typography.Title>
      {/*<img src='/SecureT.png' alt='SecureT' className='mt-10' />*/}
      <div className="col-md-12 mt-4">
        <Link to='/projects/securet/dark'>
      <button type='button' className="btn btn-dark">View Development Blog</button>
            </Link>
        </div>
        <br/>
      <ModalImage
      small={'/SecureTsmall.png'}
      large={'/SecureT.png'}
      alt="SecureT"
      />
      <a href="https://play.google.com/store/apps/details?id=securet.salehsolutions.com.securet">
        <img
        src="/play store.png"/>
      </a>
      <a href="https://apps.apple.com/us/app/id1526094461">
        <img
        src="/testflight.png"/>
      </a>
    </div>
    <Divider/>
      <div className='flex flex-col items-center' id="whatsinmyfood">
      <Typography.Title className='text-primary-color'>
        WhatsInMyFood - Enforce your diet with a press of a button
      </Typography.Title>
      <Typography.Title level={4} className='text-gray-600'>
        With WhatsInMyFood, blacklist ingredients that are not compliant to your diet and scan to see if a product has the ingredients.
      </Typography.Title>
      {/*<img src='/SecureT.png' alt='SecureT' className='mt-10' />*/}
      <div className="col-md-12 mt-4">
        <Link to='/projects/whatsinmyfood'>
      <button type='button' className="btn btn-dark">View Development Blog</button>
            </Link>
        </div>
        <br/>
      <ModalImage
      large={'/WhatsInMyFood.png'}
      alt="WhatsInMyFood"
      />
      </div>
  </LandingLayout>
)
